.outerdiv{
    flex: 1;
    border-right: 1px solid lightgray; 
    height: 100vh;
}

@media screen and (max-width: 900px) {
    .outerdiv {
        height: auto;
    }
}


.group_holder{
    padding-top: 20px;
    padding: 20px;
}

.group_child{
    background-color: #52b69a;
    display: flex;
    color:white;
    justify-content: space-between;
    margin-bottom: 10px;
    border-radius: 5px;
    padding: 30px;
    cursor: pointer;
    font-size: 20px;
}


.selected{
    color: #ff477e;
    background-color:  #fae0e4;
}

.group_child:hover{
    color: #ff477e;
    background-color: #fae0e4;
}

.profilePhoto{
    border-radius: 50%;
    width: 40px;
    height: 40px;
}


.window_outer{
    position: absolute;
    max-width: 500px;
    width:100%;
    height: 300px;
    background-color: white;
    top: 50%;
    border-radius: 7px;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index:10;
}

.window_inner{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 300px;
    height: 100%;
    margin: auto;
}

.blackCover{
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: lightgrey;
    opacity:0.5;
    filter: blur(10px);
    z-index: 8;
}

.loadingPanel{
    background-color: red;
}